import request from '@/utils/request.js'
import qs from 'qs'

export const getOrdersListApi = params => {
    return request({
        // http://kg.zhaodashen.cn/v2/orders/index.php
        url: '/v2/orders/index.php',
        params
    })
    .then(res => res.data)
}

export const getTotalApi = params => {
    return request({
        // http://kg.zhaodashen.cn/v2/orders/total.php
        url: '/v2/orders/total.php',
        params
    })
    .then(res => res.data)
}