<template>
    <el-card class="box-card">
        <div slot="header" class="clearfix header">
            <span>订单列表</span>
            <el-button type="primary" round>导出数据</el-button>
        </div>
        <div class="filter">
            <el-input placeholder="下单人" class="input-with-select" v-model="searchVal">
                <el-button slot="append" icon="el-icon-search"></el-button>
            </el-input>
            <el-input placeholder="收货人" class="input-with-select" v-model="searchVal" style="margin-left: 20px">
                <el-button slot="append" icon="el-icon-search"></el-button>
            </el-input>

            <el-date-picker
            type="daterange"
            v-model="dateVal"
            range-separator="至"
            start-placeholder="开始日期"
            end-placeholder="结束日期">
            </el-date-picker>
        </div>
        <el-table
            :data="tableData"
            stripe
            style="width: 100%">
            <el-table-column label="订单号" prop="order_sn"></el-table-column>
            <el-table-column label="下单时间" prop="add_time"></el-table-column>
            <el-table-column label="下单人" prop="username"></el-table-column>
            <el-table-column label="收货人" prop="consignee"></el-table-column>
            <el-table-column label="总额" prop="order_amount"></el-table-column>
            <el-table-column label="订单状态" prop="order_status">
                <template slot-scope="item">
                    <el-tag>{{ item.row.order_status == '1' ? '已确认': '未确认' }}</el-tag>
                </template>
            </el-table-column>
            <el-table-column label="支付状态" prop="pay_status">
                <template slot-scope="item">
                    <el-tag>{{ item.row.pay_status == '1' ? '已付款': '未付款' }}</el-tag>
                </template>
            </el-table-column>
            <el-table-column label="配送情况" prop="reg_time">
                <template slot-scope="item">
                    <el-tag>{{ item.row.shipping_status == '1' ? '配送中': '未发货' }}</el-tag>
                </template>
            </el-table-column>
            <el-table-column label="操作" fixed="right" width="120">
                <template slot-scope="item">
                    <el-button type="primary" icon="el-icon-user" @click="changeHandler(item.row)" circle></el-button>
                    <el-button type="danger" icon="el-icon-delete" @click="delHandler(item.$index)" circle></el-button>
                </template>
            </el-table-column>
        </el-table>
        
        <!-- 修改用户 -->
        <el-dialog
            title="角色分配"
            :visible.sync="isShow"
            width="30%"
            :before-close="handleClose">
                <el-form label-width="80px">
                    <el-form-item label="当前帐号">
                        <el-input v-model="form.username" disabled></el-input>
                    </el-form-item>
                    <el-form-item label="当前角色">
                        <el-input v-model="form.role_name" disabled></el-input>
                    </el-form-item>
                    <el-form-item label="选择角色">
                        <el-select v-model="form.new">
                            <el-option label="区域一" value="shanghai"></el-option>
                            <el-option label="区域二" value="beijing"></el-option>
                        </el-select>
                    </el-form-item>
                </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="isShow = false">取 消</el-button>
                <el-button type="primary" @click="isShow = false">确 定</el-button>
            </span>
        </el-dialog>

        <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="pagination.currentPage"
        :page-sizes="[5, 10, 15, 20]"
        :page-size="pagination.pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="pagination.total">
        </el-pagination>
    </el-card>
</template>
<script>
import {getOrdersListApi} from '@/api/orders.js'
export default {
    data() {
        return {
            form: {
                username: '',
                role_name: '',
                new: ''
            },
            pickerOptions: {
              shortcuts: [{
                text: '最近一周',
                onClick(picker) {
                  const end = new Date();
                  const start = new Date();
                  start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
                  picker.$emit('pick', [start, end]);
                }
              }, {
                text: '最近一个月',
                onClick(picker) {
                  const end = new Date();
                  const start = new Date();
                  start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
                  picker.$emit('pick', [start, end]);
                }
              }, {
                text: '最近三个月',
                onClick(picker) {
                  const end = new Date();
                  const start = new Date();
                  start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
                  picker.$emit('pick', [start, end]);
                }
              }]
            },
            isShow: false,
            searchVal: '',
            dateVal: '',
            pagination: {
                total: 0,
                currentPage: 1,
                pageSize: 5,
            },
            tableData: []
        }
    },
    created() {
        getOrdersListApi().then(res => {
            console.log(res);
            this.tableData = res.data.list
            this.pagination.total = parseInt(res.data.total)
        })
    },
    methods: {
        handleSizeChange(val) {
            console.log(`每页 ${val} 条`);
            this.pagination.pageSize = val
            getOrdersListApi({
                pagenum: this.pagination.currentPage,
                pagesize: val
            })
            .then(res => {
                this.tableData = res.data.list
                this.pagination.total = parseInt(res.data.total)
            })
        },
        handleCurrentChange(val) {
            // console.log(`当前页: ${val}`);
            this.pagination.currentPage = val
            getOrdersListApi({
                pagenum: val,
                pagesize: this.pagination.pageSize
            })
            .then(res => {
                this.tableData = res.data.list
                this.pagination.total = parseInt(res.data.total)
            })
        },
        delHandler(index) {
            this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                // this.$axios.delete(`/api/v2/users/delete.php?user_id=${this.tableData[index].user_id}`)
                this.tableData.splice(index, 1)
                this.$message.success('删除成功!');
            }).catch(() => {
                this.$message.info('已取消删除');          
            });
        },
        handleClose(done) {
            this.$confirm('确认关闭？')
            .then(_ => {
                done();
            })
            .catch(_ => {});
        },
        changeHandler(item) {
            this.isShow = true
            this.form.username = item.username
            this.form.role_name = item.role_name
        },

    }
}
</script>
<style scoped>
    .el-card .filter {
        margin-bottom: 20px;
        overflow: hidden
    }
    .el-card .filter .el-input {
        width: 300px;
        float: left
    } 
    .el-card .filter .el-date-editor {
        float: right;
    }
    .el-pagination {
        width: 100%;
        padding: 10px 0;
        background: #ebeef5;
        text-align: right;
        margin-top: 10px;
        padding-right: 10px;
        box-sizing: border-box;
    }
    .el-card .header .el-button {
        float: right;
        margin-top: -10px
    }
</style>